<template>
	<view class="index">
		<!-- 头部 -->
		<view class="indexHeader">
			<view class="userInfo">
				<image :src=imgSrc mode="aspectFill" class="backg" lazy-load=true></image>
				<block v-if="hasUserInfo">
					<view class="touxiang" v-if="userInfo.avatarUrl">
						<image class="userinfo-avatar" :src="userInfo.avatarUrl"></image>
					</view>
					<view class="mingzi">{{userInfo.nickName}}</view>
					<view class="geXingQingMing">{{geXing}}</view>
				</block>
			</view>
			<view class="option">
						<!-- <uni-data-checkbox  v-model="value"  :localdata="optionData" @change="checkboxChange"></uni-data-checkbox> -->
				<radio-group @change="checkboxChange">
					<label>
						<checkbox :value="sending" checked='true' :color="checkboxColor" /><text :style="{ color: optionTextColor1 }">正在寄往未来</text>
					</label>
					<label>
						<checkbox :value="finish" :color="checkboxColor"/><text :style="{ color: optionTextColor2 }">已完成</text>
					</label>
					<label>
						<checkbox :value="caogao" :color="checkboxColor"/><text :style="{ color: optionTextColor3 }">草稿箱</text>
					</label>
				</radio-group>	
			</view>
		</view>
<!-- 		<scroll-view scroll-y="true" @scroll="scroll" class="email">
			<view>asdf</view>
		</scroll-view> -->
		<!-- 邮件数据 -->
<!-- 		<view v-if="seedingFlag" class="uni-padding-wrap uni-common-mt emailList" >
			<view class="text" v-for="(num,index) in data" :key="index">正在寄往未来 - {{num}}</view>
			<view class="uni-loadmore" v-if="showLoadMore">{{loadMoreText}}</view>
		</view>
		<view v-else-if="finishFlag" class="uni-padding-wrap uni-common-mt emailList" >
			<view class="text" v-for="(num,index) in data" :key="index">已完成 - {{num}}</view>
			<view class="uni-loadmore" v-if="showLoadMore">{{loadMoreText}}</view>
		</view>
		<view v-else class="uni-padding-wrap uni-common-mt emailList" >
			<view class="text" v-for="(num,index) in data" :key="index">草稿箱 - {{num}}</view>
			<view class="uni-loadmore" v-if="showLoadMore">{{loadMoreText}}</view>
		</view> -->
		
		<!-- <view class="emailList">fuck</view> -->
		<view class="emailList">
			<my-email v-for="item in letterHeadList" v-show="item.letterState == nowType" :key="item.letterid" :ref="item.letterid" 
			:shoujianren="item.receiveUsername" :jijianren="item.sentUsername" :startTime="JSON.parse(item.startTime)" 
			:endTime="JSON.parse(item.endTime)" :beizhu="JSON.parse(item.remarks)" :stampfile="item.stampFile" :focusCss="item.letterid == currentTargetRef" @click.native="editFunction"></my-email>
		</view>
		
		<!-- 悬浮按钮 -->
		<uni-fab ref="fab" :pattern="pattern" :content="content" :horizontal="horizontal" :vertical="vertical"
			:direction="direction" @trigger="trigger" @fabClick="fabClick" />
		<!-- <view > 欢迎您， {{username}},  {{gender}}</view> -->
	</view>
</template>

<script>
	// import myEmail from '@/components/my-email/my-email.vue'
	export default {
		// components:{myEmail},
		data() {
			return {
				//头部数据
				userInfo:{},
				username: '',
				gender: '',
				hasUserInfo: false,
				geXing: '我不是口香糖 、所以不会粘着你不放',
				imgSrc: '../../static/background.jpg',
				
				//Option
				sending: 1,
				finish: 2,
				caogao: 3,
				seedingFlag: true,
				finishFlag: false,
				checkboxColor: '#1989FA',//option勾的颜色
				optionTextColor1: '#1989FA',
				optionTextColor2: '#909399',
				optionTextColor3: '#909399',
				value: 0,
				optionData: [{"value":1, "text":"寄送中"}, {"value":2, "text":"已签收"}, {"value":3, "text":"草稿箱"}],
				
				//邮件数据
				// data: [],
				// loadMoreText: "加载中...",
				// showLoadMore: false,
				// max: 0,
				letterHeadList: [],
				nowType: 1,//草稿：0发送中：1已完成：2
				letterTypeFuture: [],
				letterTypeDone: [],
				letterTypeCao: [],
				
				//悬浮按钮数据
				title: 'uni-fab',
				directionStr: '垂直',
				horizontal: 'right',
				vertical: 'bottom',
				direction: 'horizontal',
				is_color_type:false,
				pattern: {
					color: '#7A7E83',
					backgroundColor: '#fff',
					selectedColor: '#007AFF',
					buttonColor: '#1989FA',
					iconColor: '#fff'
				},
				content: [
				{
					iconPath: '/static/write.png',
					selectedIconPath: '/static/write.png',
					text: '写信',
					active: false
				}],
				
				// 用户信息
				session: '',
				
				// 选中信封ref
				currentTargetRef: '',
				lastTargetRef: '',
				
			}
		},
		methods:{
			// scroll:function(e){
			// 	console.log(e.detail.scrollTop)
			// },
			
			// 选中状态
			editFunction(e){
				// console.log(e)
				//获取块的ref, 获得聚焦
				var self = this
				this.lastTargetRef = this.currentTargetRef
				this.currentTargetRef = e.currentTarget.dataset.ref;
				
				if(this.lastTargetRef == this.currentTargetRef){
					// console.log('fuck')
					//如果是正在寄往未来的信封,是不能打开的
					if(self.nowType == 1){
						uni.showToast({
							title: '信已寄出,无法操作',
							mask: true,
							duration: 2000,
						})
					}
					else{
						//关闭当前页面，跳转到应用内的某个页面。
						uni.redirectTo({
							url: '../setemail/setemail?session=' + self.session + '&haveSave=' + 'true' + '&letterid=' + self.currentTargetRef
						})
					}
					
				}
				
			},
			
			// 邮件列表
			checkboxChange(e){				
				// this.initData()
				if(e.detail.value == 1){
					this.seedingFlag = true
					this.finishFlag = false
					this.optionTextColor1 = '#1989FA'
					this.optionTextColor2 = '#909399'
					this.optionTextColor3 = '#909399'
					this.nowType = 1
				}else if(e.detail.value == 2){
					this.seedingFlag = false
					this.finishFlag = true
					this.optionTextColor1 = '#909399'
					this.optionTextColor2 = '#1989FA'
					this.optionTextColor3 = '#909399'
					this.nowType = 2
					
				}else{
					this.seedingFlag = false
					this.finishFlag = false
					this.optionTextColor1 = '#909399'
					this.optionTextColor2 = '#909399'
					this.optionTextColor3 = '#1989FA'
					this.nowType = 0
				}
			},
			// initData(){
			// 	setTimeout(() => {
			// 		this.max = 0;
			// 		this.data = [];
			// 		let data = [];
			// 		this.max += 20;
			// 		for (var i = this.max - 19; i < this.max + 1; i++) {
			// 			data.push(i)
			// 		}
			// 		this.data = this.data.concat(data);
			// 		uni.stopPullDownRefresh();
			// 	}, 300);
			// },
			// setListData() {
			// 	let data = [];
			// 	this.max += 10;
			// 	for (var i = this.max - 9; i < this.max + 1; i++) {
			// 		data.push(i)
			// 	}
			// 	this.data = this.data.concat(data);
			// },
			// 悬浮按钮
			trigger(e) {
				// var self
				// 选中写信的按钮 (关闭当前页面，跳转到新建信封页面) redirectTo
				// console.log(e)
				this.content[e.index].active = !e.item.active
				//关闭当前页面，跳转到应用内的某个页面。
				uni.redirectTo({
					url: '../setemail/setemail?session=' + this.session + '&haveSave=' + 'false'
				})
				
				// uni.showModal({
				// 	title: '提示',
				// 	content: `您${this.content[e.index].active ? '选中了' : '取消了'}${e.item.text}`,
				// 	success: function(res) {
				// 		if (res.confirm) {
				// 			console.log('用户点击确定')
				// 		} else if (res.cancel) {
				// 			console.log('用户点击取消')
				// 		}
				// 	}
				// })
			},
			fabClick() {
				// uni.showToast({
				// 	title: '点击了悬浮按钮',
				// 	icon: 'none'
				// })
			},
			switchBtn(hor, ver) {
				if (hor === 0) {
					this.direction = this.direction === 'horizontal' ? 'vertical' : 'horizontal'
					this.directionStr = this.direction === 'horizontal' ? '垂直' : '水平'
				} else {
					this.horizontal = hor
					this.vertical = ver
				}
				this.$forceUpdate()
			},
			switchColor(){
				this.is_color_type = !this.is_color_type
				if(this.is_color_type) {
					this.pattern.iconColor = '#aaa'
					this.pattern.buttonColor = '#fff'
				}else{
					this.pattern.iconColor = '#fff'
					this.pattern.buttonColor = '#007AFF'
				}
			},
			
			//获取信封信息
			getLetterHead(session){
				var self = this
				uni.request({
					url:'https://afflatus.xyz/getIndex',
					method:'POST',
					data:{
						session
					},
					success:function(res){
						if(res.data.status==200){
							self.letterHeadList = res.data.letterhead
							console.log(self.letterHeadList)
							
						}else{
							uni.redirectTo({
								url:"../login/login"
							})
						}
					},
					fail: function(){
						uni.redirectTo({
							url:"../login/login"
						})
					}
				})
			}
			
			
		},
		
		onLoad:function(){
			var self = this;
			
			//加载邮箱数据
			// this.initData();
			
			//显示主页的头部数据
			uni.getStorage({
				key:'userInfo',
				success:function(res){
					self.userInfo = res.data
					self.hasUserInfo = true
				},
				fail:function(){
					uni.redirectTo({
						url:"../login/login"
					})
				}
			})
			
			//检查storage中是否有session，
			//有则向服务器发送请求，确定是否过期，有过期则跳转到登录页面，无则读取用户相关数据
			//无则跳转到登录页面
			//[ 未完成 ]添加axios拦截器
			uni.getStorage({
				key:'third_session',
				success:function(res){
					console.log('fuck')
					var session = res.data
					uni.request({
						url:'https://afflatus.xyz/queryToken',
						method:'POST',
						data:{
							session
						},
						success:function(res){
							if(res.data.status==200){
								// self.username = res.data.userInfo.username;
								// self.gender = res.data.userInfo.gender;
								// console.log("获取到服务器的用户数据")
								// console.log(self.username, self.gender)
								console.log('token 有效')
								self.session = session
								console.log(res)
								// 获取信封信息
								self.getLetterHead(session)
								
							}else{
								console.log('token 无效')
								uni.redirectTo({
									url:"../login/login"
								})
							}
						},
						fail() {
							console.log('fuck2')
						}
					})
				},
				fail:function(){
					console.log("无third_session")
					uni.redirectTo({
						url:"../login/login"
					})
				}
			})
			
			//方便调试，跳转到写信页面。
			// uni.navigateTo({
			// 	url: '../setemail/setemail'
			// })
			
		},
		//邮件列表
		// onUnload() {
		// 	this.max = 0,
		// 	this.data = [],
		// 	this.loadMoreText = "加载更多",
		// 	this.showLoadMore = false;
		// },
		// onReachBottom() {
		// 	console.log("onReachBottom");
		// 	if (this.max > 40) {
		// 		this.loadMoreText = "没有更多数据了!"
		// 		return;
		// 	}
		// 	this.showLoadMore = true;
		// 	setTimeout(() => {
		// 		this.setListData();
		// 	}, 300);
		// },
		// onPullDownRefresh() {
		// 	console.log('onPullDownRefresh');
		// 	this.initData();
		// },
		
		// 悬浮按钮
		onBackPress() {
			if (this.$refs.fab.isShow) {
				this.$refs.fab.close()
				return true
			}
			return false
		}
		
	}
</script>

<style>

.index{
	height: 100%;
	width: 100%;
}

.indexHeader{
	position: fixed;
	left: 0px;
	top: 0px;
	width: 100%;
	height: 432.69rpx;
	background-color: #FFFFFF;
	z-index: 2;
}

.userInfo{
	position: relative;
	height: 342.3rpx;
	background-color: #fff;
	overflow: hidden;
	box-shadow: 0px 9.61rpx 13.46rpx #afafaf;
}

.backg{
	/* height:100%; */
	width: 100%;
	opacity:0.7;
}

.touxiang{
	position: absolute;
	left: 0px;
	top: 58.3rpx;
	width: 100%;
	text-align:center;
	margin-top: 15.38rpx;
}

.userinfo-avatar {
	border-radius: 88.46rpx;
	width: 88.46rpx;
	height: 88.46rpx;
}

.mingzi{
	position: absolute;
	left: 0px;
	top: 52rpx;
	margin-top: 111.53rpx;
	font-size: 26.92rpx;
	width: 100%;
	text-align: center;
	color: #000000; 
}

.geXingQingMing{
	position: absolute;
	left: 0rpx;
	top: 50rpx;
	margin-top: 146.15rpx;
	font-size: 30.76rpx;
	width: 100%;
	text-align: center;
	color: #4E4E4E;
}

.index .option{
	margin-top: 30rpx; //31.735
	width: 100%;
	text-align: center;
	font-size: 26.92rpx;
}

.option checkbox{
	/* width: 26.92rpx;
	height: 26.92rpx; */
	transform:scale(0.7);
/* 	color: #F0F2F5;
 */}

.option label:nth-child(1){
	margin-right: 70rpx;
}

.option label:nth-child(2){
	margin-right: 70rpx;
}

.emailList{
/* 	position: fixed;
	left: 0px; */
	padding-top: 449.69rpx;
	background-color: #FcFcFc;
}

.index .text {
	margin: 16rpx 0;
	width:100%;
	background-color: #fff;
	height: 120rpx;
	line-height: 120rpx;
	text-align: center;
	color: #555;
	border-radius: 8rpx;
}

.option checkbox .wx-checkbox-input{
	/* border: #000000 !important; */
	/* border-radius: 50%; */
	border: 1px solid #D5D8DE;
	background-color: #F0F2F5;
}


</style>
